import * as React from 'react'
import { Slot } from '@radix-ui/react-slot'
import { cn } from '$Popup/lib/utils'
import BGSvgIcon from '$Popup/components/core/BGSvgIcon'
import BGSpinner from '$Popup/components/core/BGSpinner'

// Manual variant definitions based on the commented code
const getButtonClasses = (
  variant: string,
  size: string,
  loading?: boolean,
  disabled?: boolean
) => {
  const variants = {
    primary: 'bg-primary text-backdrop',
    secondary: 'bg-transparent text-primary border border-primary',
    tertiary: 'text-primary',
    orange: 'bg-button-orange text-tragic',
  }

  const sizes = {
    default: 'w-[200px]',
    small: 'w-[40px]',
    large: 'w-[375px]',
  }

  const stateClasses =
    disabled
    ? 'cursor-not-allowed opacity-50'
    : ''

  return cn(
    'base-button',
    variants[variant as keyof typeof variants],
    sizes[size as keyof typeof sizes],
    stateClasses
  )
}

function Button({
  className,
  variant = 'primary',
  size = 'default',
  asChild = false,
  iconName,
  children,
  loading = false,
  disabled = false,
  ...props
}: React.ComponentProps<'button'> & {
  variant?: 'primary' | 'orange' | 'tertiary' | 'secondary'
  size?: 'default' | 'small' | 'large'
  asChild?: boolean
  iconName?: string
  loading?: boolean
  disabled?: boolean
}) {
  const Comp = asChild ? Slot : 'button'
  const isDisabled = disabled || loading

  return (
    <Comp
      data-slot="base-button"
      className={cn(
        getButtonClasses(variant, size, loading, isDisabled),
        className
      )}
      disabled={isDisabled}
      {...props}
    >
      <>
        {loading ? (
          <BGSpinner  />
        ) : (
          iconName && <BGSvgIcon iconName={iconName} size={18} />
        )}
        {children}
      </>
    </Comp>
  )
}

export default Button
