import { chrome } from "@/utils/polyfill";

export async function injectScript(
  tabId: number,
  filename: string,
  world: "MAIN" | "ISOLATED",
  injectImmediately: boolean = true,
): Promise<boolean> {
  try {
    if (await isFileInjected(tabId, filename)) {
      console.debug(`OAP: File ${filename} already injected`);
      return true;
    }

    await chrome.scripting.executeScript({
      target: { tabId: tabId, allFrames: false },
      files: [filename],
      world: world,
      injectImmediately: injectImmediately,
    });

    // inject a flag to the window to indicate that the script has been injected
    await chrome.scripting.executeScript({
      target: { tabId: tabId, allFrames: false },
      func: (filename: string) => {
        const camelCaseFileName = filename
          .replace(/-/g, "")
          .replace(/\b\w/g, (char) => char.toUpperCase());
        window[camelCaseFileName] = true;
      },
      args: [filename],
      injectImmediately: true,
    });
    console.debug(`OAP: File ${filename} successfully injected`);
  } catch (error) {
    console.error("OAP: Error injecting script", error);
    return false;
  }
  return true;
}

async function isFileInjected(
  tabId: number,
  filename: string
): Promise<boolean> {
  const scriptRes = await chrome.scripting.executeScript({
    target: { tabId: tabId, allFrames: false },
    func: (filename: string) => {
      const camelCaseFileName = filename
        .replace(/-/g, "")
        .replace(/\b\w/g, (char) => char.toUpperCase());
      return window[camelCaseFileName];
    },
    args: [filename],
    injectImmediately: true,
  });
  if (scriptRes.length === 0) {
    return false;
  }

  const { result } = scriptRes[0];
  return result ?? false;
}

export async function openTab(url: string): Promise<chrome.tabs.Tab> {
  return new Promise((res, rej) => {
    chrome.tabs.create({ url, active: true }, (tab) => {
      if (chrome.runtime.lastError) {
        rej(chrome.runtime.lastError.message);
      } else {
        res(tab);
      }
    });
  });
}

// wait until the current tab is on the page we want
// If the page is already loaded, return immediately
// loadingCb is a callback that is called while the page is loading. Useful for showing a loading UI.
export async function waitUntilPageLoaded(
  tab: chrome.tabs.Tab,
  urlToLoad: string,
  loadingCb?: () => void,
  doPrecheck: boolean = true
) {

  if (!urlToLoad.startsWith('http')) {
    urlToLoad = `https://${urlToLoad}`;
  }
  
  const url = new URL(urlToLoad);
  const expectedHostname = url.hostname;
  const expectedPath = url.pathname;

  if (doPrecheck) {
    // first check if the user is already on the page we want
    const currLocation = tab.url;
    if (!!currLocation) {
      const uri = new URL(currLocation);
      const hostname = uri.hostname;
      const path = uri.pathname;
      if (hostname === expectedHostname && path === expectedPath) {
        console.debug("OAP: Page loaded");
        return;
      }
    }
  }

  const completer = new Completer<void>();

  const listenerFunc = async (
    tabId: number,
    changeInfo: chrome.tabs.TabChangeInfo,
    tab: chrome.tabs.Tab,    
  ) => {
    if (tabId !== tab.id!) {
      return;
    }

    if (changeInfo.status !== 'complete') {
      return;
    }

    const currLocation = tab.url;
    if (!currLocation) {
      return;
    }

    const uri = new URL(currLocation);
    const hostname = uri.hostname;
    const path = uri.pathname;
    if (hostname === expectedHostname && path === expectedPath) {
      console.debug("OAP: Page loaded");
      completer.resolve(undefined);
      return;
    }    

    loadingCb?.();
  }

  chrome.tabs.onUpdated.addListener(listenerFunc);

  await completer.future;

  chrome.tabs.onUpdated.removeListener(listenerFunc);
  console.debug("OAP: Page loaded successfully", urlToLoad);
}



// Completer is a helper class to wait for a value to be resolved (A rough implementation of the Dart Completer https://api.flutter.dev/flutter/dart-async/Completer-class.html)
export class Completer<T> {
  promise: Promise<T>
  resolve!: (value: T | PromiseLike<T>) => void
  reject!: (reason?: any) => void

  constructor() {
    this.promise = new Promise((resolve, reject) => {
      this.resolve = resolve
      this.reject = reject
    })
  }

  get future() {
    return this.promise
  }
}
