import React, { useState } from "react";
import ReactDOM from "react-dom/client";
// Import processed CSS as a string
import cssText from "@/app/popup/src/styles/css-exporter.js";
import { BGButton, BGCheckBox, BGSvgIcon } from "../../../components/core";
import { Completer } from "@/app/scripts/oap/utils";
import { t } from "$Popup/i18n";

console.log("OAP: oap-action-notice.tsx loaded");

const extensionRoot = chrome.runtime.getURL('app/popup/');

async function createOAPActionNotice(title: string) {
    const completer = new Completer<{ dontShowAgain?: boolean, cancel?: boolean }>();
    const shadowHost = document.createElement("div");
    shadowHost.id = "bg-oap-action-notice";

    // inject it in the <html> tag
    document.documentElement.appendChild(shadowHost);
    const shadowRoot = shadowHost.attachShadow({ mode: "open" });

    // Inject compiled CSS into shadow DOM
    try {
        const styleSheet = document.createElement("style");
        styleSheet.textContent = cssText;
        shadowRoot.appendChild(styleSheet);
        console.log("OAP: CSS injected successfully", cssText.length, "characters");
    } catch (error) {
        console.error("OAP: Error injecting CSS:", error);
    }

    const reactContainer = document.createElement("div");
    shadowRoot.appendChild(reactContainer);

    const root = ReactDOM.createRoot(reactContainer);
    root.render(
        <OAPActionNotice title={title} onComplete={(dontShowAgain, cancel) => {
            completer.resolve({ dontShowAgain, cancel });
        }} />
    );

    const result = await completer.future;
    dismissOAPActionNotice();
    return result;
}

const OAPActionNotice: React.FC<{ title: string, onComplete: (dontShowAgain?: boolean, cancel?: boolean) => void }> = ({ title, onComplete }) => {
    return (
        <div>
            {/* Backdrop */}
            <OAPActionNoticeBackdrop />
            {/* Modal Container */}
            <OAPActionNoticeModalContent title={title} onComplete={onComplete} />
        </div>
    );
}

const OAPActionNoticeBackdrop: React.FC = () => {
    return (
        <div className="fixed top-0 left-0 right-0 bottom-0 w-full h-full z-[2147483610] flex items-center justify-center bg-[rgba(13,25,47,0.5)] font-roboto">
        </div>
    );
}

const OAPActionNoticeModalContent: React.FC<{ title: string, onComplete: (dontShowAgain?: boolean, cancel?: boolean) => void }> = ({ title, onComplete }) => {
    let [dontShowAgain, setDontShowAgain] = useState(false);

    const handleDontShowAgainChange = (checked: boolean) => {
        setDontShowAgain(checked);
    }

    const handleLetsGoClicked = () => {
        onComplete(dontShowAgain, false);
    }

    return (
        <div className="fixed top-0 left-0 right-0 bottom-0 w-full flex flex-col items-center justify-center h-full bg-transparent z-[2147483611]">
            <div className="relative flex flex-col items-center justify-center p-8 rounded-2xl bg-backdrop shadow-xl w-[569px]">
                <button className="absolute top-4 right-4" onClick={() => {
                    onComplete(undefined, true);
                }}>
                    <BGSvgIcon iconName="close" width={24} height={24} iconRoot={extensionRoot} />
                </button>
                <div className="flex flex-col space-y-15 items-center">
                    {/* Title, image, text and checkbox */}
                    <div className="flex flex-col space-y-5 text-tragic items-center">
                        {/* <h3 className="text-[22px] font-medium leading-7">{t('OAPActionNoticeTitle')}</h3> */}
                        <h3 className="text-[22px] font-medium leading-7">{title}</h3>
                        <BGSvgIcon iconName="oap-action-notice" width={576} height={296} iconRoot={extensionRoot} />
                        <p className="text-base font-normal leading-6 tracking-[0.15px] text-justify opacity-80">{t('OAPActionNoticeDescription')}</p>
                        <div className="flex flex-row items-center gap-2">
                            <BGCheckBox checked={dontShowAgain} onCheckedChange={handleDontShowAgainChange} />
                            <p className="text-sm font-normal leading-5 tracking-[0.25px]">{t('OAPActionNoticeDontShowAgain')}</p>
                        </div>
                    </div>
                    {/* Let's go button */}
                    <BGButton onClick={handleLetsGoClicked}>
                        {t('OAPActionNoticeLetsGo')} <BGSvgIcon iconName="arrow-right" width={18} height={18} iconRoot={extensionRoot} />
                    </BGButton>

                </div>
            </div>
        </div>
    );
}

function dismissOAPActionNotice() {
    const shadowHost = document.documentElement.querySelectorAll("#bg-oap-action-notice");
    if (shadowHost.length > 0) {
        for (let i = 0; i < shadowHost.length; i++) {
            shadowHost[i].remove();
        }
    }
}

// @ts-ignore
window.createOAPActionNotice = createOAPActionNotice;
// @ts-ignore
window.dismissOAPActionNotice = dismissOAPActionNotice;

export { createOAPActionNotice, dismissOAPActionNotice, OAPActionNoticeModalContent, OAPActionNoticeBackdrop };